import React, { FunctionComponent } from 'react';
import { NavHashLink } from 'react-router-hash-link';
import { withNaming } from '@bem-react/classname';
import classNames from 'classnames';
import { GoBriefcase, GoHome } from 'react-icons/go';
import bemPreset from '../../utils/bemPreset';

const Navbar: FunctionComponent = () => {
    const cn = withNaming(bemPreset)('navbar');
    const styleSheet = classNames(cn());

    return (
        <nav className={styleSheet}>
            <div className={cn('wrapper')}>
                <NavHashLink
                    className={cn('link')}
                    activeClassName={cn('link-active')}
                    scroll={(el) => el.scrollIntoView({ behavior: 'smooth' })}
                    to="/#summary"
                >
                    <GoHome className={cn('icon')} />
                    <span className={cn('text')}>Summary</span>
                </NavHashLink>
                <NavHashLink
                    className={cn('link')}
                    activeClassName={cn('link-active')}
                    scroll={(el) => el.scrollIntoView({ behavior: 'smooth' })}
                    to="/#contact"
                >
                    <GoBriefcase className={cn('icon')} />
                    <span className={cn('text')}>Contact</span>
                </NavHashLink>
                <NavHashLink
                    className={cn('link')}
                    activeClassName={cn('link-active')}
                    scroll={(el) => el.scrollIntoView({ behavior: 'smooth' })}
                    to="/#work"
                >
                    <GoBriefcase className={cn('icon')} />
                    <span className={cn('text')}>Work</span>
                </NavHashLink>
                <NavHashLink
                    className={cn('link')}
                    activeClassName={cn('link-active')}
                    scroll={(el) => el.scrollIntoView({ behavior: 'smooth' })}
                    to="/#education"
                >
                    <GoBriefcase className={cn('icon')} />
                    <span className={cn('text')}>Education</span>
                </NavHashLink>
            </div>
        </nav>
    );
};

export default Navbar;
